	module.exports = {"data":{"allTalkJson":{"edges":[{"node":{"description":"By popular demand, Defcon's angry little roboticist is back with more stories of robot designs gone awry that make practical lessons on making better robots. Drinking will happen: vodka-absconding scoundrels are not invited.\n\nThis talk will cover material assuming the average audience member is a relatively intelligent coder with a high-school physics/math background and has seen linear algebra/calculus before. The intent is to navigate people new to robotics around many lessons my teams and I learned the \"hard way,\" and to introduce enough vocabulary for a self-teaching student to bridge the gap between amateur and novice professional robotics. It will not cover why your Arduino doesn't work when you plugged your USB tx into your RS232 tx.","meta":{"duration":"PT49M51S"},"presenter":{"name":null},"source":{"id":"gFW0schumkE","provider":"youtube"},"slug":"katy-levinson-defcon-20-robots-youre-still-doing-it-wrong","tag":["Robot","Robots","Katy Levinson","DEFCON 20"],"thumbnails":{"default":{"url":"https://i.ytimg.com/vi/gFW0schumkE/default.jpg","height":90,"width":120}},"title":"Katy Levinson Defcon 20 - Robots: You're Still Doing It Wrong"}}]}},"pathContext":{"slug":"robots","tag":"Robots"}}

